export const getProfiles = () => dispatch => {
    dispatch(setProfileLoading());
    axios
      .get('/api/profile/all')
       .then(res =>
        dispatch({
          type: GET_PROFILES,
          payload: res.data
        })
      )
      .catch(err =>
        dispatch({
          type: GET_PROFILES,
          payload:err.response.data
        })
      );
  };
export const getProfileByHandle = (handle) =>dispatch =>{
    dispatch(setProfileLoading());
    axios.get(`/api/profile/handle/${handle}`)
         .then(res=>
            dispatch({
                type:GET_PROFILE,
                payload:res.data 
            }
            )).catch(err=>
            dispatch({
                type:GET_PROFILES,
                payload:null
            }
            ))
}

export const deleteEducation =(id) =>dispatch =>{
  
    axios.delete(`/api/profile/education/${id}`)
    .then(res =>
        dispatch({
            type:GET_PROFILE,
            payload:res.data
        })
        )
    .catch(err=>
       dispatch({
           type:GET_ERRORS,
           payload:err.response.data 
       }
       ))  
}


